if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseToolbar', component.exports)
}
export default {
  props: {
    menu: {
      type: Boolean,
      default: true
    },
    logoPath: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      height: 60,
      drawer: null
    }
  },
  computed: {
    hpixel() {
      return `${this.height}px`
    },
    logoHpixel() {
      return `${this.height * 0.6}px`
    },
    logoSrc() {
      return this.logoPath ? this.logoPath : process.env.VUE_APP_LOGO_URL
    }
  }
}
